import React, { useState } from 'react';
import db from '../dexie';
import '../style/staffPage.css'; // Import CSS file for styling

const StaffPage = ({ onLogin }) => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleLogin = async () => {
    try {
      // Authenticate user
      const user = await db.users.where('username').equals(username).and(user => user.password === password).first();
      if (user) {
        // Check admin rights
        if (user.admin) {
          // If the user is an admin, redirect to admin page
          onLogin(user.username, true);
        } else {
          // If the user is not an admin, redirect to staff page
          onLogin(user.username, false);
        }
      } else {
        setError('Invalid username or password.');
      }
    } catch (error) {
      console.error('Error during login:', error);
      setError('An error occurred during login. Please try again later.');
    }
  };

  return (
    <div className="login-container">
      <h2>Staff Login</h2>
      <input type="text" placeholder="Username" value={username} onChange={(e) => setUsername(e.target.value)} />
      <input type="password" placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)} />
      <button className="login-button" onClick={handleLogin}>Login</button>
      {error && <p className="error-message">{error}</p>}
    </div>
  );
};

export default StaffPage;
