import Dexie from 'dexie';

const db = new Dexie('EventBookingDB');

db.version(2.3).stores({
  users: '++id, username, password, admin',
  customers: '++id, name, phone, email',
  hallLists: '++id, hallname',
  eventTimes: '++id, timeshiftname, timestart, timeend',
  foodLists: '++id, foodname, category, foodprice',
  softDrinks: '++id, sdrinkname',
  hardDrinks: '++id, hdrinkname',
  packages: '++id, packagename, food, sdrink, hdrink',
  categories: '++id, categoryname', 
  bookings: '++id, customerId, hallName, eventDate, startTime, endTime, packageName, customPackageName, misc, selectedFood, selectedSoftDrinks, selectedHardDrinks, guests, totalcost, addedFood, extraPrice, extraInfo',
});

export default db;
