import React, { useState } from 'react';
import './App.css';

// Import components
import StaffPage from './component/staffPage';
import AdminMenu from './component/adminMenu';
import NewBooking from './component/newBooking';
import ViewBooking from './component/viewBooking';
import ViewCustomer from './component/viewCustomer';
import Math from './component/math';

const App = () => {
  const [activeTab, setActiveTab] = useState('staffPage');

  const handleTabChange = (newTab) => {
    setActiveTab(newTab);
  };

  return (
    <div className="app-container">
      <nav className="navbar">
        <button className={activeTab === 'staffPage' ? 'active' : ''} onClick={() => handleTabChange('staffPage')}>Login</button>
        <button className={activeTab === 'adminMenu' ? 'active' : ''} onClick={() => handleTabChange('adminMenu')}>Admin Menu</button>
        <button className={activeTab === 'newBooking' ? 'active' : ''} onClick={() => handleTabChange('newBooking')}>New Booking</button>
        <button className={activeTab === 'viewBooking' ? 'active' : ''} onClick={() => handleTabChange('viewBooking')}>View Bookings</button>
        <button className={activeTab === 'viewCustomer' ? 'active' : ''} onClick={() => handleTabChange('viewCustomer')}>Customers</button>
        <button className={activeTab === 'math' ? 'active' : ''} onClick={() => handleTabChange('math')}>Math</button>

      </nav>

      {/* Render components based on activeTab */}
      {activeTab === 'staffPage' && <StaffPage />}
      {activeTab === 'adminMenu' && <AdminMenu />}
      {activeTab === 'newBooking' && <NewBooking />}
      {activeTab === 'viewBooking' && <ViewBooking />}
      {activeTab === 'viewCustomer' && <ViewCustomer />}
      {activeTab === 'math' && <Math />}

    </div>
  );
};

export default App;
