// components/ViewCustomers.js
import React, { useState, useEffect } from 'react';
import db from '../dexie';
import '../style/viewCustomer.css'; // Import CSS file

const ViewCustomers = () => {
  const [customers, setCustomers] = useState([]);

  useEffect(() => {
    // Fetch customers from the database
    const fetchCustomers = async () => {
      const customersFromDB = await db.customers.toArray();
      // Sort customers alphabetically by name
      const sortedCustomers = customersFromDB.sort((a, b) => a.name.localeCompare(b.name));
      setCustomers(sortedCustomers);
    };
    fetchCustomers();
  }, []);

  return (
    <div className="view-customers"> 
      <h2 className="view-customer-heading"> Customers List</h2>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Phone</th>
            <th>Email</th>
          </tr>
        </thead>
        <tbody>
          {customers.map((customer) => (
            <tr key={customer.id}>
              <td>{customer.name}</td>
              <td>{customer.phone}</td>
              <td>{customer.email}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default ViewCustomers;
