
import React, { useState, useEffect } from 'react';
import db from '../dexie';
import '../style/adminMenu.css';

const AdminMenu = () => {
  const [staffList, setStaffList] = useState([]);
  const [hallList, setHallList] = useState([]);
  const [eventTimes, setEventTimes] = useState([]);
  const [foodPackages, setFoodPackages] = useState([]);
  const [foodLists, setFoodLists] = useState([]);
  const [softDrinks, setSoftDrinks] = useState([]);
  const [hardDrinks, setHardDrinks] = useState([]);
  const [selectedFood, setSelectedFood] = useState([]);
  const [selectedSoftDrinks, setSelectedSoftDrinks] = useState([]);
  const [selectedHardDrinks, setSelectedHardDrinks] = useState([]);

  const [selectedPackage, setSelectedPackage] = useState('');
  const [showStaffSection, setShowStaffSection] = useState(false);
  const [showHallSection, setShowHallSection] = useState(false);
  const [showEventTimeSection, setShowEventTimeSection] = useState(false);
  const [showFoodListSection, setShowFoodListSection] = useState(false);
  const [showSoftDrinkSection, setShowSoftDrinkSection] = useState(false);
  const [showHardDrinkSection, setShowHardDrinkSection] = useState(false);
  const [showFoodPackageSection, setShowFoodPackageSection] = useState(false);
  const [showSelectionSection, setShowSelectionSection] = useState(false);
  const [showCategorySection, setShowCategorySection] = useState(false); // Add this line
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('');



  useEffect(() => {
    const fetchData = async () => {
      const staff = await db.users.toArray();
      const halls = await db.hallLists.toArray();
      const times = await db.eventTimes.toArray();
      const packages = await db.packages.toArray();
      const food = await db.foodLists.toArray();
      const soft = await db.softDrinks.toArray();
      const hard = await db.hardDrinks.toArray();
      const categories = await db.categories.toArray();

      setStaffList(staff);
      setHallList(halls);
      setEventTimes(times);
      setFoodPackages(packages);
      setFoodLists(food);
      setSoftDrinks(soft);
      setHardDrinks(hard);
      setCategories(categories);
    };

    fetchData();
  }, []);

  const addStaff = async (name, password, isAdmin) => {
    try {
      await db.users.add({ username: name, password, admin: isAdmin });
      const updatedStaff = await db.users.toArray();
      setStaffList(updatedStaff);
    } catch (error) {
      console.error('Error adding staff:', error);
    }
  };

  const deleteStaff = async (id) => {
    try {
      await db.users.delete(id);
      const updatedStaff = staffList.filter((staff) => staff.id !== id);
      setStaffList(updatedStaff);
    } catch (error) {
      console.error('Error deleting staff:', error);
    }
  };

  const addHall = async (name) => {
    try {
      await db.hallLists.add({ hallname: name });
      const updatedHalls = await db.hallLists.toArray();
      setHallList(updatedHalls);
    } catch (error) {
      console.error('Error adding hall:', error);
    }
  };

  const deleteHall = async (id) => {
    try {
      await db.hallLists.delete(id);
      const updatedHalls = hallList.filter((hall) => hall.id !== id);
      setHallList(updatedHalls);
    } catch (error) {
      console.error('Error deleting hall:', error);
    }
  };

  const addEventTime = async (name, startTime, endTime) => {
    try {
      await db.eventTimes.add({ timeshiftname: name, timestart: startTime, timeend: endTime });
      const updatedEventTimes = await db.eventTimes.toArray();
      setEventTimes(updatedEventTimes);
    } catch (error) {
      console.error('Error adding event time:', error);
    }
  };

  const deleteEventTime = async (id) => {
    try {
      await db.eventTimes.delete(id);
      const updatedEventTimes = eventTimes.filter((time) => time.id !== id);
      setEventTimes(updatedEventTimes);
    } catch (error) {
      console.error('Error deleting event time:', error);
    }
  };
  const addCategory = async (categoryName) => {
    try {
      await db.categories.add({ categoryname: categoryName });
      const updatedCategories = await db.categories.toArray();
      setCategories(updatedCategories);
    } catch (error) {
      console.error('Error adding category:', error);
    }
  };

  const addFoodList = async (name, price) => {
    try {
      await db.foodLists.add({ foodname: name, category: selectedCategory, foodprice: price });
      const updatedFoodLists = await db.foodLists.toArray();
      setFoodLists(updatedFoodLists);
    } catch (error) {
      console.error('Error adding food list:', error);
    }
  };
  
  const addSoftDrink = async (name) => {
    try {
      await db.softDrinks.add({ sdrinkname: name });
      const updatedSoftDrinks = await db.softDrinks.toArray();
      setSoftDrinks(updatedSoftDrinks);
    } catch (error) {
      console.error('Error adding soft drink:', error);
    }
  };

  const addHardDrink = async (name) => {
    try {
      await db.hardDrinks.add({ hdrinkname: name });
      const updatedHardDrinks = await db.hardDrinks.toArray();
      setHardDrinks(updatedHardDrinks);
    } catch (error) {
      console.error('Error adding hard drink:', error);
    }
  };

  const deleteFoodItem = async (id) => {
    try {
      await db.foodLists.delete(id);
      const updatedFoodLists = foodLists.filter((food) => food.id !== id);
      setFoodLists(updatedFoodLists);
    } catch (error) {
      console.error('Error deleting food item:', error);
    }
  };

  const deleteSoftDrink = async (id) => {
    try {
      await db.softDrinks.delete(id);
      const updatedSoftDrinks = softDrinks.filter((soft) => soft.id !== id);
      setSoftDrinks(updatedSoftDrinks);
    } catch (error) {
      console.error('Error deleting soft drink:', error);
    }
  };

  const deleteHardDrink = async (id) => {
    try {
      await db.hardDrinks.delete(id);
      const updatedHardDrinks = hardDrinks.filter((hard) => hard.id !== id);
      setHardDrinks(updatedHardDrinks);
    } catch (error) {
      console.error('Error deleting hard drink:', error);
    }
  };

  const deleteCategory = async (id) => {
    try {
      await db.categories.delete(id);
      const updatedCategories = categories.filter((category) => category.id !== id);
      setCategories(updatedCategories);
    } catch (error) {
      console.error('Error deleting category:', error);
    }
  };



  const handleSubmitPackage = async () => {
    try {
      if (selectedPackage) {
        // Check if a package with the selected name already exists
        const existingPackage = foodPackages.find(pkg => pkg.packagename === selectedPackage);

        if (existingPackage) {
          // Update existing package with selected items
          const updatedPackage = {
            ...existingPackage,
            food: selectedFood,
            sdrink: selectedSoftDrinks,
            hdrink: selectedHardDrinks
          };
          await db.packages.put(updatedPackage); // Use put() to update existing package
          alert('Package updated successfully!');
        } else {
          // Create a new package
          await addFoodPackage(selectedPackage, selectedFood, selectedSoftDrinks, selectedHardDrinks);
          alert('Package added successfully!');
        }

        // Clear selected items after adding/updating package
        setSelectedPackage(null);
        setSelectedFood([]);
        setSelectedSoftDrinks([]);
        setSelectedHardDrinks([]);
      } else {
        alert('Please select a package name.');
      }
    } catch (error) {
      console.error('Error adding package:', error);
    }
  };

  const addFoodPackage = async (packageName, selectedFood, selectedSoftDrinks, selectedHardDrinks) => {
    try {
      const foodPackage = {
        packagename: packageName,
        food: selectedFood,
        sdrink: selectedSoftDrinks,
        hdrink: selectedHardDrinks,
      };
      await db.packages.add(foodPackage);
      const updatedFoodPackages = await db.packages.toArray();
      setFoodPackages(updatedFoodPackages);
    } catch (error) {
      console.error('Error adding food package:', error);
    }
  };

  const deleteFoodPackage = async (id) => {
    try {
      await db.packages.delete(id);
      const updatedFoodPackages = foodPackages.filter((food) => food.id !== id);
      setFoodPackages(updatedFoodPackages);
    } catch (error) {
      console.error('Error deleting food package:', error);
    }
  };
  const handleFoodCheckboxChange = (foodName) => {
    setSelectedFood(prevSelectedFood => {
      if (prevSelectedFood.includes(foodName)) {
        return prevSelectedFood.filter(item => item !== foodName);
      } else {
        return [...prevSelectedFood, foodName];
      }
    });
  };

  const handleSoftDrinkCheckboxChange = (softDrinkName) => {
    setSelectedSoftDrinks(prevSelectedSoftDrinks => {
      if (prevSelectedSoftDrinks.includes(softDrinkName)) {
        return prevSelectedSoftDrinks.filter(item => item !== softDrinkName);
      } else {
        return [...prevSelectedSoftDrinks, softDrinkName];
      }
    });
  };

  const handleHardDrinkCheckboxChange = (hardDrinkName) => {
    setSelectedHardDrinks(prevSelectedHardDrinks => {
      if (prevSelectedHardDrinks.includes(hardDrinkName)) {
        return prevSelectedHardDrinks.filter(item => item !== hardDrinkName);
      } else {
        return [...prevSelectedHardDrinks, hardDrinkName];
      }
    });
  };


  // Group food items by category
  const groupedFoodByCategory = foodLists.reduce((acc, food) => {
    if (!acc[food.category]) {
      acc[food.category] = [];
    }
    acc[food.category].push(food);
    return acc;
  }, {});




  return (
    <div className="admin-menu-container">
      <h2 className="admin-menu-heading">Admin Menu</h2>

      {/* Section 1: Add Staff */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowStaffSection(!showStaffSection)}>Add Staff</h3>
        {showStaffSection && (
          <>
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const name = e.target.name.value;
              const password = e.target.password.value;
              const isAdmin = e.target.isAdmin.checked;
              addStaff(name, password, isAdmin);
              e.target.reset();
            }}>
              <input type="text" name="name" placeholder="Staff Name" required />
              <input type="password" name="password" placeholder="Password" required />
              <label>
                Admin:
                <input type="checkbox" name="isAdmin" />
              </label>
              <button className="admin-staffButton" type="submit">Submit Staff</button>
            </form>

            {/* Staff List */}
            <h3 className="admin-menu-subheading">Staff List</h3>
            <ul className="admin-menu-list">
              {staffList.map((staff) => (
                <li key={staff.id}>
                  {staff.username} - Admin: {staff.admin ? 'Yes' : 'No'}
                  <button className="delete-button" onClick={() => deleteStaff(staff.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 2: Add Hall */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowHallSection(!showHallSection)}>Add Hall</h3>
        {showHallSection && (
          <>
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const name = e.target.name.value;
              addHall(name);
              e.target.reset();
            }}>
              <input type="text" name="name" placeholder="Hall Name" required />
              <button className="admin-menuButton" type="submit">Submit Hall</button>
            </form>

            {/* Hall List */}
            <h3 className="admin-menu-subheading">Hall List</h3>
            <ul className="admin-menu-list">
              {hallList.map((hall) => (
                <li key={hall.id}>
                  {hall.hallname}
                  <button className="delete-button" onClick={() => deleteHall(hall.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 3: Add Event Time */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowEventTimeSection(!showEventTimeSection)}>Add Event Time</h3>
        {showEventTimeSection && (
          <>
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const name = e.target.name.value;
              const startTime = e.target.startTime.value;
              const endTime = e.target.endTime.value;
              addEventTime(name, startTime, endTime);
              e.target.reset();
            }}>
              <input type="text" name="name" placeholder="Event Time Name" required />
              <input type="time" name="startTime" className="time" placeholder="Start Time" required />
              <input type="time" name="endTime" className="time" placeholder="End Time" required />
              <button className="admin-menuButton" type="submit">Submit Event Time</button>
            </form>

            {/* Event Time List */}
            <h3 className="admin-menu-subheading">Event Time List</h3>
            <ul className="admin-menu-list">
              {eventTimes.map((time) => (
                <li key={time.id}>
                  {time.timeshiftname} - {time.timestart} to {time.timeend}
                  <button className="delete-button" onClick={() => deleteEventTime(time.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 4: Add Food List */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowFoodListSection(!showFoodListSection)}>Add Food List</h3>
        {showFoodListSection && (
          <>
           <form className="admin-menu-form" onSubmit={(e) => {
  e.preventDefault();
  const name = e.target.querySelector('.foodname-input').value;
  const price = parseFloat(e.target.querySelector('.foodprice-input').value);
  addFoodList(name, price);
  e.target.reset();
}}>
  <input type="text" className="foodname-input" name="name" placeholder="Food Name" required />
  <input type="number" className="foodprice-input" name="price" placeholder="Price" required />
  {/* Select category */}
  <select name="category" className='foodname-input' value={selectedCategory} onChange={(e) => setSelectedCategory(e.target.value)}>
    <option value="">Select Category</option>
    {categories.map(category => (
      <option key={category.id} value={category.categoryname}>{category.categoryname}</option>
    ))}
  </select>
  <button className="admin-menuButton" type="submit">Submit Food</button>
</form>


            {/* Food List */}
            <h3 className="admin-menu-subheading">Food List</h3>
            <ul className="admin-menu-list">
              {foodLists.map((food) => (
                <li key={food.id}>
                  {food.foodname} - Category: {food.category} -  [Rs: {food.foodprice}]
                  <button className="delete-button" onClick={() => deleteFoodItem(food.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 5: Add Soft Drink */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowSoftDrinkSection(!showSoftDrinkSection)}>Add Soft Drink</h3>
        {showSoftDrinkSection && (
          <>
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const name = e.target.name.value;
              addSoftDrink(name);
              e.target.reset();
            }}>
              <input type="text" name="name" placeholder="Soft Drink Name" required />
              <button className="admin-menuButton" type="submit">Submit Soft Drink</button>
            </form>

            {/* Soft Drink List */}
            <h3 className="admin-menu-subheading">Soft Drink List</h3>
            <ul className="admin-menu-list">
              {softDrinks.map((soft) => (
                <li key={soft.id}>{soft.sdrinkname}
                  <button className="delete-button" onClick={() => deleteSoftDrink(soft.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 6: Add Hard Drink */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowHardDrinkSection(!showHardDrinkSection)}>Add Hard Drink</h3>
        {showHardDrinkSection && (
          <>
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const name = e.target.name.value;
              addHardDrink(name);
              e.target.reset();
            }}>
              <input type="text" name="name" placeholder="Hard Drink Name" required />
              <button className="admin-menuButton" type="submit">Submit Hard Drink</button>
            </form>

            {/* Hard Drink List */}
            <h3 className="admin-menu-subheading">Hard Drink List</h3>
            <ul className="admin-menu-list">
              {hardDrinks.map((hard) => (
                <li key={hard.id}>{hard.hdrinkname}
                  <button className="delete-button" onClick={() => deleteHardDrink(hard.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 7: Add Food Package */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowFoodPackageSection(!showFoodPackageSection)}>Add Food Package</h3>
        {showFoodPackageSection && (
          <>
            {/* Add Food Package Form */}
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const name = e.target.name.value;
              addFoodPackage(name);
              e.target.reset();
            }}>
              <input type="text" name="name" placeholder="Food Package Name" required />
              <button className="admin-menuButton" type="submit">Submit Food Package</button>
            </form>

            {/* Food Package List */}
            <h3 className="admin-menu-subheading">Food Package List</h3>
            <ul className="admin-menu-list">
              {foodPackages.map((foodPackage) => (
                <li key={foodPackage.id}>
                  {foodPackage.packagename}
                  <button className="delete-button" onClick={() => deleteFoodPackage(foodPackage.id)}>Delete</button>
                </li>
              ))}
            </ul>
            {/* View Packages */}
            <h3 className="admin-menu-subheading">View Packages</h3>
            <ul>
              {foodPackages.map((pkg) => (
                <div className="packhead" key={pkg.id} style={{ marginTop: '20px' }}>
                  <b>{pkg.packagename}</b>
                  <ul>
                    {Object.entries(groupedFoodByCategory).map(([category, foods]) => (
                      <div key={category}>
                        <h4>{category}</h4>
                        <ul>
                          {/* Display food items for the current category and current package */}
                          {pkg.food && pkg.food.map((foodName) => {
                            const food = foods.find(f => f.foodname === foodName);
                            return food && <li key={food.id}>{food.foodname}</li>;
                          })}
                        </ul>
                      </div>
                    ))}
                  </ul>
                  <div>
                    <li><b>Soft Drinks:</b> {pkg.sdrink ? pkg.sdrink.join(', ') : ''}</li>
                    <li><b>Hard Drinks:</b> {pkg.hdrink ? pkg.hdrink.join(', ') : ''}</li>
                  </div>
                </div>
              ))}
            </ul>
          </>
        )}
      </div>

      {/* Section 8: Edit Food Package */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowSelectionSection(!showSelectionSection)}>Edit Food Package</h3>
        {showSelectionSection && (
          <>
            {/* Select Food Package */}
            <select className="select-package" onChange={(e) => setSelectedPackage(e.target.value)} value={selectedPackage}>
              <option value="">Select Package</option>
              {foodPackages.map((foodPackage) => (
                <option key={foodPackage.id} value={foodPackage.packagename}>{foodPackage.packagename}</option>
              ))}
            </select>

            {/* Food Items */}
            {Object.entries(groupedFoodByCategory).map(([category, foods]) => (
              <div key={category}>
                <h3>{category}</h3>
                <ul>
                  {foods.map((food) => (
                    <li key={food.id}>
                      {food.foodname}
                      <input
                        type="checkbox"
                        onChange={() => handleFoodCheckboxChange(food.foodname)}
                        checked={selectedFood.includes(food.foodname)}
                      />
                    </li>
                  ))}
                </ul>
              </div>
            ))}
            {/* Soft Drinks */}
            <div>
              <h4>Soft Drinks:</h4>
              {softDrinks.map((soft) => (
                <label key={soft.id}>
                  <input
                    type="checkbox"
                    value={soft.sdrinkname}
                    checked={selectedSoftDrinks.includes(soft.sdrinkname)}
                    onChange={(e) => handleSoftDrinkCheckboxChange(e.target.value)}
                  />
                  {soft.sdrinkname}
                </label>
              ))}
            </div>

            {/* Hard Drinks */}
            <div>
              <h4>Hard Drinks:</h4>
              {hardDrinks.map((hard) => (
                <label key={hard.id}>
                  <input
                    type="checkbox"
                    value={hard.hdrinkname}
                    checked={selectedHardDrinks.includes(hard.hdrinkname)}
                    onChange={(e) => handleHardDrinkCheckboxChange(e.target.value)}
                  />
                  {hard.hdrinkname}
                </label>
              ))}
            </div>

            {/* Submit Button */}
            <button className="admin-saveButton" onClick={handleSubmitPackage}>Save Changes</button>
          </>
        )}
      </div>

      {/* Section 9: Add Category */}
      <div className="admin-section">
        <h3 className="admin-menu-subheading" onClick={() => setShowCategorySection(!showCategorySection)}>Add Category</h3>
        {showCategorySection && (
          <>
            <form className="admin-menu-form" onSubmit={(e) => {
              e.preventDefault();
              const categoryName = e.target.categoryName.value;
              addCategory(categoryName);
              e.target.reset();
            }}>
              <input type="text" name="categoryName" placeholder="Category Name" required />
              <button className="admin-menuButton" type="submit">Submit Category</button>
            </form>

            {/* Category List */}
            <h3 className="admin-menu-subheading">Category List</h3>
            <ul className="admin-menu-list">
              {categories.map((category) => (
                <li key={category.id}>{category.categoryname}
                  <button className="delete-button" onClick={() => deleteCategory(category.id)}>Delete</button>
                </li>
              ))}
            </ul>
          </>
        )}
      </div>
    </div>
  );
};

export default AdminMenu;
