import React, { useState, useEffect } from 'react';
import db from '../dexie';
import '../style/math.css'; // Import CSS file


const Math = () => {
  const [monthlyTotals, setMonthlyTotals] = useState(Array(12).fill(0));
  const [totalOfTotals, setTotalOfTotals] = useState(0);
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear());

  useEffect(() => {
    const fetchData = async () => {
      try {
        const bookings = await db.bookings.toArray();
        const monthlyTotalsCopy = Array(12).fill(0);

        bookings.forEach(booking => {
          const bookingDate = new Date(booking.eventDate);
          if (bookingDate.getFullYear() === selectedYear) {
            const month = bookingDate.getMonth();
            monthlyTotalsCopy[month] += booking.totalcost || 0;
          }
        });

        const total = monthlyTotalsCopy.reduce((acc, val) => acc + val, 0);
        setTotalOfTotals(total);
        setMonthlyTotals(monthlyTotalsCopy);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, [selectedYear]);

  const handleYearChange = (e) => {
    setSelectedYear(parseInt(e.target.value));
  };

  return (
    <div className="math-container">
      <h1 className="math-heading">Monthly Total Cost for {selectedYear}</h1>
      <div className="year-label">
        Select Year:{' '}
        <select className='select-year' value={selectedYear} onChange={handleYearChange}>
          {Array.from({ length: new Date().getFullYear() - 2000 + 1 }, (v, k) => 2000 + k).map(year => (
            <option key={year} value={year}>
              {year}
            </option>
          ))}
        </select>
      </div>
      <table className="math-table">
        <thead>
          <tr>
            <th>Month</th>
            <th>Collection</th>
          </tr>
        </thead>
        <tbody>
          {monthlyTotals.map((total, index) => (
            <tr key={index}>
              <td>{new Date(selectedYear, index).toLocaleString('default', { month: 'long' })}</td>
              <td>Rs {total.toLocaleString()}</td>
            </tr>
          ))}
          <tr>
            <td>Yearly Total</td>
            <td>Rs {totalOfTotals.toLocaleString()}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default Math;
