import React, { useState, useEffect } from 'react';
import { createRoot } from 'react-dom/client';
import db from '../dexie';
import '../style/viewBooking.css';
import emailjs from 'emailjs-com';



const BookingPopup = ({ booking }) => {
  const [selectedPackageDetails, setSelectedPackageDetails] = useState(null);
  const [addedFoodNames, setAddedFoodNames] = useState([]);

  const [selectedFoodNames, setSelectedFoodNames] = useState([]);
  const [selectedSoftDrinkNames, setSelectedSoftDrinkNames] = useState([]);
  const [selectedHardDrinkNames, setSelectedHardDrinkNames] = useState([]);
  const [foodLists, setFoodLists] = useState([]);
  const [foodCategories, setFoodCategories] = useState([]);




  // Function to categorize food items
  const categorizeFoodItems = () => {
    const categorizedFood = {};
    // Categorize food items
    foodLists.forEach(food => {
      if (categorizedFood[food.category]) {
        categorizedFood[food.category].push(food);
      } else {
        categorizedFood[food.category] = [food];
      }
    });
    // Convert categorized food to array format
    const categorizedFoodArray = Object.entries(categorizedFood).map(([category, items]) => ({
      category,
      items,
    }));
    // Set categorized food categories
    setFoodCategories(categorizedFoodArray);
  };

  useEffect(() => {
    // Call categorizeFoodItems when foodLists changes
    categorizeFoodItems();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [foodLists]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const foodData = await db.foodLists.toArray();
        setFoodLists(foodData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };
    fetchData();
  }, []);

  useEffect(() => {
    const fetchPackageDetails = async () => {
      try {
        let packageData = null;
        if (booking.packageName) {
          packageData = await db.packages.where('packagename').equals(booking.packageName).first();
        } else if (booking.customPackageName) {
          packageData = await db.packages.where('packagename').equals(booking.customPackageName).first();
        }
        console.log('Package Data:', packageData);
        if (packageData) {
          setSelectedPackageDetails({
            packagename: packageData.packagename,
            food: packageData.food,
            sdrink: packageData.sdrink,
            hdrink: packageData.hdrink
          });
        } else {
          setSelectedPackageDetails(null);
        }
      } catch (error) {
        console.error('Error fetching package details:', error);
      }
    };

    const fetchSelectedItems = async () => {
      try {
        const foodNames = await Promise.all(booking.selectedFood.map(async foodId => {
          const foodItem = await db.foodLists.get(foodId);
          return foodItem ? foodItem.foodname : null;
        }));

        const sdrinkNames = await Promise.all(booking.selectedSoftDrinks.map(async sdrinkId => {
          const sdrinkItem = await db.softDrinks.get(sdrinkId);
          return sdrinkItem ? sdrinkItem.sdrinkname : null;
        }));

        const hdrinkNames = await Promise.all(booking.selectedHardDrinks.map(async hdrinkId => {
          const hdrinkItem = await db.hardDrinks.get(hdrinkId);
          return hdrinkItem ? hdrinkItem.hdrinkname : null;
        }));



        setSelectedFoodNames(foodNames.filter(name => name !== null));
        setSelectedSoftDrinkNames(sdrinkNames.filter(name => name !== null));
        setSelectedHardDrinkNames(hdrinkNames.filter(name => name !== null));

      } catch (error) {
        console.error('Error fetching selected items:', error);
      }
    };

    const fetchAddedItems = async () => {
      try {
        const afoodNames = await Promise.all(booking.addedFood.map(async foodId => {
          const foodItem = await db.foodLists.get(foodId);
          return foodItem ? foodItem.foodname : null;
        }));

        setAddedFoodNames(afoodNames.filter(name => name !== null));

      } catch (error) {
        console.error('Error fetching added foods:', error);
      }
    };


    if (booking.packageName || booking.customPackageName) {
      fetchPackageDetails();
      fetchAddedItems(); // Call fetchAddedItems here
    } else {
      setSelectedPackageDetails(null);
    }


    if (booking.selectedFood && booking.selectedFood.length > 0) {
      fetchSelectedItems();
    }
  }, [booking.packageName, booking.customPackageName, booking.selectedFood, booking.selectedSoftDrinks, booking.selectedHardDrinks, booking.addedFood]);

  // Function to send booking confirmation email using EmailJS
  const sendBookingConfirmationEmail = () => {
    // Extracted data from the render part
    const recipientName = booking.customer?.name;
    const bookingId = booking.id;
    const packageName = booking.packageName || booking.customPackageName;
  
    // Check if food items, soft drinks, and hard drinks are available in selectedPackageDetails
    let foodItems = '';
    let softDrinks = '';
    let hardDrinks = '';
  
    if (selectedPackageDetails) {
      // Use food, sdrink, and hdrink from selectedPackageDetails
      foodItems = selectedPackageDetails.food.join(', ');
      softDrinks = selectedPackageDetails.sdrink ? selectedPackageDetails.sdrink.join(', ') : 'N/A';
      hardDrinks = selectedPackageDetails.hdrink ? selectedPackageDetails.hdrink.join(', ') : 'N/A';
    } else {
      // Use values from individual states
      foodItems = selectedFoodNames.join(', ');
      softDrinks = selectedSoftDrinkNames.join(', ');
      hardDrinks = selectedHardDrinkNames.join(', ');
    }
  
    const addedFood = addedFoodNames.join(', ');
    const misc = booking.misc || 'N/A';
    const extraExpense = booking.extraInfo || 'N/A';
    const grandTotal = booking.totalcost;

    // Populate EmailJS template with extracted data
    const templateParams = {
      recipientName: recipientName,
      bookingId: bookingId,
      packageName: packageName,
      foodItems: foodItems,
      softDrinks: softDrinks,
      hardDrinks: hardDrinks,
      addedFood: addedFood,
      misc: misc,
      extraExpense: extraExpense,
      grandTotal: grandTotal
    };

    // Send email using EmailJS
    
    emailjs.send('service_qm2tybi', 'template_i3q2qgj', templateParams, '90O3yCKn4GkfV2Mm4')
      .then((response) => {
        alert('Email sent successfully:')
        console.log('Email sent successfully:', response);
      })
      .catch((error) => {
        alert('Error Sending Email')
        console.error('Error sending email:', error);
      });
  };



  return (
    <div className="booking-popup">
      {booking && (
        <div>
          <div className="booking-header">
            <h3 style={{ display: 'inline-block', marginLeft: '5px' }}>Booking Details</h3>

          </div>
          <table className="booking-table" style={{ borderCollapse: 'collapse', width: '100%' }}>
            <tbody>
              {Object.entries({
                'Customer Name': booking.customer?.name,
                'Customer Phone': booking.customer?.phone,
                'Email': booking.customer?.email,
                'Hall': booking.hallName,
                'No of Guest': booking.guests,
                'Event Date': booking.eventDate,
                'Time': `${booking.startTime} - ${booking.endTime}`,
                'Package': booking.packageName || booking.customPackageName,
                ...selectedPackageDetails && {
                  'Food': (
                    <div className="package-details">
                      {foodCategories.map(({ category }) => (
                        <div key={category}>
                          <h4 style={{ marginBottom: '0' }}>{category}</h4>
                          <div className="food-columns">
                            {foodLists
                              .filter(food => food.category === category && selectedPackageDetails.food.includes(food.foodname))
                              .map(food => (
                                <div key={food.id} className="food-column">
                                  {food.foodname}
                                </div>
                              ))}
                          </div>
                        </div>
                      ))}

                    </div>
                  ),
                  'Added Food': (
                    <>
                      {foodCategories.map(({ category, items }) => {
                        const categoryFoods = items.filter(food => addedFoodNames.includes(food.foodname));
                        if (categoryFoods.length > 0) {
                          return (
                            <div key={category}>
                              <h4 style={{ marginBottom: '0' }}>{category}</h4>
                              <div className="food-columns">
                                {categoryFoods.map(food => (
                                  <div key={food.id} className="food-column">
                                    {food.foodname}
                                  </div>
                                ))}
                              </div>
                            </div>
                          );
                        }
                        return null;
                      })}
                    </>
                  ),

                  'Soft Drinks': selectedPackageDetails.sdrink ? selectedPackageDetails.sdrink.join(', ') : 'N/A',
                  'Hard Drinks': selectedPackageDetails.hdrink ? selectedPackageDetails.hdrink.join(', ') : 'N/A'
                },
                ...(!selectedPackageDetails && {
                  'Selected Food': (
                    <>
                      {foodCategories.map(({ category, items }) => (
                        <div key={category}>
                          <h4 style={{ marginBottom: '0' }}>{category}</h4>
                          <div className="food-columns">
                            {items
                              .filter(food => selectedFoodNames.includes(food.foodname))
                              .map(food => (
                                <div key={food.id} className="food-column">
                                  {food.foodname}
                                </div>
                              ))}
                          </div>
                        </div>
                      ))}
                    </>
                  ),
                  'Soft Drinks': selectedSoftDrinkNames.length > 0 ? selectedSoftDrinkNames.join(', ') : 'N/A',
                  'Hard Drinks': selectedHardDrinkNames.length > 0 ? selectedHardDrinkNames.join(', ') : 'N/A'
                }),
                ...booking.misc && { 'Misc': booking.misc },
                ...booking.extraInfo && { 'Extra Expense': booking.extraInfo },
                ...booking.extraPrice && { 'Extra Cost': booking.extraPrice },
                'Grand Total': booking.totalcost
              }).map(([label, value], index) => (
                <tr key={index} style={{ backgroundColor: index % 2 === 0 ? '#f2f2f2' : '#ffffff' }}>
                  <td style={{ border: '1px solid #ddd', padding: '4px' }}><b>{label}:</b></td>
                  <td style={{ border: '1px solid #ddd', padding: '4px' }}>{value}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

<button
  className="submit-button"
  onClick={sendBookingConfirmationEmail}
  style={{ backgroundColor: '#3c90ea', color: '#fff', padding: '10px 20px', borderRadius: '5px', border: 'none', cursor: 'pointer',marginTop: '10px' }}
>
  Send Email
</button>
    </div>
  );

};

const ViewBookings = () => {
  const [bookingsByDate, setBookingsByDate] = useState({});
  const [currentDate, setCurrentDate] = useState(new Date());

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const bookingsData = await db.bookings.toArray();

        // Fetch customer details for each booking
        const bookingsWithCustomers = await Promise.all(bookingsData.map(async booking => {
          const customer = await db.customers.get(booking.customerId);
          return { ...booking, customer }; // Add customer details to booking
        }));

        const bookingsByDate = {};

        // Organize bookings by date
        bookingsWithCustomers.forEach(booking => {
          const date = booking.eventDate;
          if (!bookingsByDate[date]) {
            bookingsByDate[date] = [];
          }
          bookingsByDate[date].push(booking);
        });

        setBookingsByDate(bookingsByDate);
      } catch (error) {
        console.error('Error fetching bookings:', error);
      }
    };

    fetchBookings();
  }, []);

  // Function to get the number of days in a month
  const getDaysInMonth = (year, month) => {
    return new Date(year, month + 1, 0).getDate();
  };

  // Function to get the name of the month
  const getMonthName = (month) => {
    const months = [
      'January', 'February', 'March', 'April', 'May', 'June', 'July',
      'August', 'September', 'October', 'November', 'December'
    ];
    return months[month];
  };

  // Function to get the bookings for a specific date
  const getBookingsForDate = (date) => {
    return bookingsByDate[date] || [];
  };

  // Function to navigate to the previous month
  const goToPreviousMonth = () => {
    setCurrentDate(prevDate => new Date(prevDate.getFullYear(), prevDate.getMonth() - 1, 1));
  };

  // Function to navigate to the next month
  const goToNextMonth = () => {
    setCurrentDate(prevDate => new Date(prevDate.getFullYear(), prevDate.getMonth() + 1, 1));
  };

  // Get the number of days in the current month
  const numDaysInMonth = getDaysInMonth(currentDate.getFullYear(), currentDate.getMonth());

  const handleBookingClick = (booking) => {
    const popup = window.open('', '_blank', 'width=600,height=600');
    popup.document.body.innerHTML = '<div id="popup-container"></div>';
    const popupContainer = popup.document.getElementById('popup-container');
    createRoot(popupContainer).render(
      <BookingPopup booking={booking} />
    );
  };

  return (
    <div className="view-bookings-container">
      <h2 className="view-booking-heading">View Bookings</h2>

      <h2 className="view-book-label">{getMonthName(currentDate.getMonth())}, {currentDate.getFullYear()}</h2>
      <div className="calendar">
        {/* Navigation buttons */}
        <div className="calendar-nav">
          <button className='mbutton' onClick={goToPreviousMonth}>Previous Month</button>
          <button className='mbutton' onClick={goToNextMonth}>Next Month</button>
        </div>

        {/* Render each day in the month */}
        <div className="calendar-grid">
          {Array.from({ length: numDaysInMonth }, (_, index) => {
            const day = index + 1;
            const date = new Date(currentDate.getFullYear(), currentDate.getMonth(), day);
            // Subtract one day from the date
            date.setDate(date.getDate() + 1);
            const dateString = date.toISOString().split('T')[0]; // Get the date in 'YYYY-MM-DD' format
            const bookings = getBookingsForDate(dateString);
            return (
              <div key={dateString} className="calendar-day">
                <h3>{day}</h3>
                {bookings.length > 0 && (

                  <table className="booking-table">
                    <thead>
                      <tr>
                        <th>Customer Name</th>
                        <th>Customer Phone</th>
                        <th>Hall</th>
                        <th>Time</th>
                        <th>Package</th>
                        {/* Add more table headers if needed */}
                      </tr>
                    </thead>
                    <tbody>
                      {bookings.map(booking => (
                        <tr key={booking.id} onClick={() => handleBookingClick(booking)} style={{ cursor: 'pointer' }}>
                          <td>{booking.customer?.name}</td>
                          <td>{booking.customer?.phone}</td>
                          <td>{booking.hallName}</td>
                          <td>{booking.startTime} - {booking.endTime}</td>
                          <td>{booking.packageName || booking.customPackageName}</td>
                          {/* Add more table cells if needed */}
                        </tr>
                      ))}
                    </tbody>
                  </table>

                )}
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default ViewBookings;
